<template>
  <!--  -->
  <!--  -->
  <header id="headerNav" :style="{ color: color }">
    <!--  -->
    <div v-if="to !== false">
      <router-link :to="to" tag="div">
        <span class="iconfont back">&#xe766;</span>
      </router-link>
    </div>
    <!--  -->
    <div v-else>
      <span class="iconfont back" @touchend="back">
        &#xe766;
      </span>
    </div>
    <!-- title -->
    <p class="title">{{ name }}</p>
    <!--  -->
    <span v-if="seach" class="iconfont seach">&#xe612;</span>
    <!--  -->
    <span v-if="menu" class="iconfont menu">&#xe6a9;</span>
  </header>
</template>

<script>
export default {
  name: 'head-nav',
  inheritAttrs: false,
  props: {
    // 
    name: {
      type: [String, Object],
      default: ''
    },
    // 
    color: {
      type: String,
      default: '#000'
    },
    // 
    to: {
      type: [Object, String, Boolean],
      default: () => {}
    },
    // 
    seach: {
      type: Boolean,
      default: false
    },
    // 
    menu: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    back() {
      this.$emit('back')
    }
  }
}
</script>

<style lang="scss" scoped>
#headerNav {
  height: 4rem;
  width: 90%;
  line-height: 4rem;
  display: flex;
  margin: auto;
}
.back,
.seach,
.menu {
  font-size: 3rem;
}
.back {
  flex: 2;
}
.title {
  flex: 6;
  font-size: 2rem;
  padding-left: 2rem;
}
span {
  text-align: center;
}
.seach {
  flex: 2;
}
.menu {
  flex: 1;
}
</style>
