import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/scss/global.scss'
import 'assets/scss/resetUi.scss'
// lodash
import upperFirst from 'lodash/upperFirst'
import camelCase from 'lodash/camelCase'
// toast
import ToastObj from 'plugins/toast/index.js'
// 
import VueLazyload from 'vue-lazyload'
const loading = require('./assets/images/loading/loading.gif')

Vue.use(ToastObj)
Vue.use(VueLazyload, {
  preLoad: 1.3,
  loading: loading.default,
  attempt: 1
})

// 
const requireComponent = require.context(
  // 
  './components/common',
  // 
  false,
  // 
  /[A-Z]\w+\.(vue|js)$/
)

requireComponent.keys().forEach(fileName => {
  // 
  const componentConfig = requireComponent(fileName)

  //  PascalCase 
  const componentName = upperFirst(
    camelCase(
      // 
      fileName
        .split('/')
        .pop()
        .replace(/\.\w+$/, '')
    )
  )

  // 
  Vue.component(
    componentName,
    //  `export default` 
    //  `.default`
    // 
    componentConfig.default || componentConfig
  )
})

Vue.config.productionTip = false
// 
Vue.filter('shear', function(origin) {
  var num = 0
  if (origin >= 10000) {
    num = Math.round(origin / 10000) + ''
    return num
  } else {
    return origin
  }
})

// 
Vue.filter('format', function(interval) {
  interval = Math.floor(interval)
  const minute = Math.floor(interval / 60)
  const second = Math.floor(interval % 60)
    .toString()
    .padStart(2, '0')
  return `${minute}:${second}`
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
