import Vue from 'vue'
import ToastMessage from './Toast.vue'

// 
const ToastStructure = Vue.extend(ToastMessage)
// 
let toast
// 
let timer = null

const toastObj = {
  install() {
    Vue.prototype.$toast = this.useToast
  },

  useToast(options) {
    // 
    if (!toast) {
      // 
      toast = new ToastStructure()
      // 
      toast.$mount(document.createElement('div'))
      // 
      document.body.appendChild(toast.$el)
    }
    // 
    if (timer) {
      clearTimeout(timer)
      timer = null
      toast.message = ''
    }
    // 
    if (typeof options === 'string') {
      toast.message = options
    } else if (typeof options === 'object') {
      const { message, time } = options
      toast.message = message
      toast.time = time || 3000
    } else {
      return false
    }
    // toast
    toast.isToast = true
    // 
    timer = setTimeout(() => {
      toast.isToast = false
      toast.message = ''
      clearTimeout(timer)
      timer = null
    }, toast.time)
  }
}
export default toastObj
