import axios from 'request/http'

const playerPageApi = {
  // url
  getMusicUrl: id => {
    return axios.get(`/song/url?id=${id}`)
  },
  // 
  getSongDetails: id => {
    return axios.get(`/song/detail?ids=${id}`)
  },

  // 
  getLyric: id => {
    return axios.get(`/lyric?id=${id}`)
  }
}

export default playerPageApi
